import React from "react";
import { animated, useTrail, useSpring } from 'react-spring'
import { Route, NavLink, HashRouter } from "react-router-dom"

import Home from '../home'
import Industry from '../industry'
import Academia from '../academia'
import Projects from '../projects'
import Studio from '../studio'
import Contact from '../contact'

const from = index => ({ opacity: 0 })
const to = index => ({ opacity: 1 })

function NavBar() {

    const spring = useSpring({
        config: { duration: 600 },
        from: { opacity: 0 },
        to: { backgroundColor: "rgba(227,228,230, 0.05)", opacity: 1 }
    })

    const [trail, set] = useTrail(data_forNavBar.length, index => ({
        config: {
            mass: 10,
            tension: 900,
            friction: 200
        },
        from: from(index), ...to(index)
    }))
        
    return(
      <HashRouter>
        <div>
          <Route path="/" exact component={ Home } />
          <Route path="/home" component={ Home } />
          <Route path="/industry" component={ Industry } />
          <Route path="/academia" component={ Academia } />
          <Route path="/projects" component={ Projects } />
          <Route path="/studio" component={ Studio } />
          <Route path="/contact" component={ Contact } />
        </div>
        <animated.div class="navbar-container" style={ spring }>
          { trail.map(({ opacity }, index) => (
              <animated.div key={ data_forNavBar[index] } style={{ opacity }}>
                <ul>
                  <li>
                    <NavLink exact to={ data_forNavBar[index].link }>
                      <svg class="navbar-element" viewBox="0 0 512 512">
                        <path
                            fill="rgba(227,228,230, 0.8)"
                            d={ data_forNavBar[index].svg }/>
                      </svg>
                    </NavLink>
                  </li>
                </ul>
              </animated.div>
          ))}
        </animated.div>
      </HashRouter>
    )
}

