        content5: `Output is being recorded in ,outfile5665 Program #2${'\n\n'}Enter a date (MM/DD/YYYY):${'\n'}12/07/1941${'\n\n'}That day is a Sunday.${'\n\n\n\u2022'} Here is a simple formula to solve the problem:${'\n'}   Input:${'\n'}       Month    2 digit month${'\n'}       Day      2 digit day${'\n'}       Year     4 digit year${'\n\n'}   The Formula:${'\n'}       a = (14 - month) / 12${'\n'}       y = year - a${'\n'}       m = month + 12 * a - 2${'\n'}       d = (day + y + y / 4 - y  / 100 + y / 400 + (31 * m / 12) ) % 7${'\n\n\u2022'} The value of d is the answer, and represents the number of the day of the week, with Sunday = 0.${'\n\u2022'} The value of d is 0 for a Sunday, 1 for a Monday, 2 for a Tuesday, etc.${'\n\u2022'} Remember that all division is integer division.`,
        ruler: ` 1${'\n'} 2${'\n'} 3${'\n'} 4${'\n'} 5${'\n'} 6`
