        heading2: 'Program #3',
        content2: 'Making Change',
        heading3: 'Summary:',
        content3: `This assignment is designed to help you learn about flow control in assembler. For this program, you will accept a currency input, and you will output the appropriate list of bills and coins equal to the input amount.${'\n\n'}Additionally, you must make your program as efficient as possible. The grader will count the number of instructions in your program (not counting macros). The fewer instructions you have, the better. Very efficient programs may receive up to 10% extra credit, and very inefficient programs may be penalized by up to 10%.`,
        heading4: `${'\n'}Details:`,
        content4: `${'\u2022'} First, your program will print the Programming Assignment number, your name, and your class account, all on one line. ${'\n\n\u2022'} Then your program will print "Enter an amount in U.S. Dollars (no $ sign): " as a prompt for the user to enter the amount.${'\n\n\u2022'} Your program will calculate the bills and coints to equal the input amount.${'\n\n\u2022'} Your program will then print "That amount is " followed by the list of bill and coins required.${'\n\n\u2022'} Your format should match the example that follows exactly.${'\n\n\u2022'} Your program must output the fewest possible bills and coins. That is, $30 must be output as 1 x $20 and 1 x $10, not 3 x $10.${'\n\n\u2022'} Your program must validate input. That means a dollar amount, a period and then a two digit cents amount.${'\n\n\u2022'} Remember that any time you read input from the keyboard via linein you must have allocated a buffer of at least 80 bytes.${'\n\n\u2022'} Your answer output should be one denomination per line. You must not print a line with a zero quantity, such as: 0 x $50${'\n\n\u2022'} Valid bills: $100, $50, $20, $10, $5, $1. Valid coins: 50c, 25c, 10c, 5c, 1c. Other forms may exist, but are uncommon, and may not be used. (No dollar coins.)${'\n\n\u2022'} Remember that all strings you print must be null terminated.${'\n\n\u2022'} Should an error in input be detected, you must print a helpful error message and again prompt the user to enter a currency amount.${'\n\n\u2022'} ADDENDUM: Valid inputs are values between 0.01 and 32767.99`
