const dataFor_Course_04_Prog_01 = [
    {
        id: 2,
        heading2: 'Lab #1',
        content2: 'Get Familiar with LogicWorks',
        heading5: `Solution:`,
        content5: `Coming Soon...${'\n'}Coming Soon...${'\n'}Coming Soon...${'\n'}Coming Soon...${'\n'}Coming Soon...${'\n'}Coming Soon...${'\n'}Coming Soon...${'\n'}Coming Soon...${'\n'}Coming Soon...${'\n'}Coming Soon...${'\n'}Coming Soon...${'\n'}Coming Soon...${'\n'}Coming Soon...${'\n'}Coming Soon...${'\n'}Coming Soon...`
    },
    {
        id: 1,
        heading2: 'Lab #1',
        content2: 'Get Familiar with LogicWorks',
        heading3: 'Objective:',
        content3: 'LogicWorks is an interactive circuit design tool that allows you to run quick and efficient simulations on screen. It’s the fastest solution to help you learn digital logic. The purpose of this lab is to let you get familiar with the LogicWorks software. Note that this lab assignment is individual assignment, which means each student needs to complete it independently.',
        heading4: `${'\n'}Problem Statement:`,
        content4: `1. Create a 4-1 MUX using ONLY tri-state buffers and inverters in LogicWorks. This circuit will select one output from four inputs based on two addition switches. Please read textbook page 131 section 3-9 Multiplexers before implementing this lab assignment. You can test out your circuit using binary switches and a Hex keyboard (use Ctrl-F to show signal values). Please read Page 131 for detailed information about MUX (Multiplexer).${'\n\n'}    a. This MUX should have two selector lines S0 and S1 and four input lines A-D.${'\n\n'}    b. The output should match the following table:${'\n'}        S1 S0 || OUTPUT${'\n'}        ---------------${'\n'}        0  0  ||   A${'\n'}        0  1  ||   B${'\n'}        1  0  ||   C${'\n'}        1  1  ||   D${'\n'}In other words, when S0 and S1 are both zero the MUX will select input A for use as the output.${'\n\n'}2. Use the created circuit from step 1 to create a part in LogicWorks. This can be accomplished using a LogicWorks part called "port in."${'\n\n'}    a. You will need a port in for each of the six inputs. Label each port in using the text${'\n'}    tool (Ctrl – E). Make sure you click the port in part and name them with their appropriate${'\n'}    name such as S0, S1, A... and so on. Note that the text labels should become purple.${'\n\n'}    b. There will be one output line which will require the use of a "port out" part in${'\n'}    LogicWorks. Name it as you like. Use the same method above to label the port out part.${'\n\n'}    c. Save your LogicWorks file and leave it as an open circuit.${'\n\n'}3. With the circuit from step 2 create a part and save it into a library file.${'\n\n'}    a. Open a new device symbol. (File->New->Device Symbol)${'\n\n'}    b. Select the Options menu, and then "Subcircuit and Parts Type". This opens another${'\n'}    window. Select "Create a sub-circuit symbol and select an open circuit to attach to it."${'\n'}    Click the subcircuit that you have created in Step 2.${'\n\n'}    c. Auto-create a device symbol if desired. (Under Options menu)${'\n\n'}    d. Layout the part graphic as desired. If you want you can create almost any shape in${'\n'}    LogicWorks since it is only a logical representation and not an actual part.${'\n'}    Ensure pins are in the correct location.${'\n\n'}    e. Save the part to a new library (File->Save). The name of the library file should be the${'\n'}    first initial of your first name and your last name. For example the grader\'s library file${'\n'}    would be called jdoe.clf since his full name is John Doe.${'\n\n'}Additional Notes:${'\n\u2022'} You can add more parts to this library for later use.${'\n\u2022'} One useful device produces a constant 1 signal. It can be created it by using an inverted ground.${'\n\u2022'} Remember to always test your circuit for the correct output.`
    }
]

const dataFor_Course_04_Prog_02 = [
    {
        id: 2,
        heading2: 'Lab #2',
        content2: 'Basic Circuits',
        heading5: `Solution:`,
        content5: `Coming Soon...${'\n'}Coming Soon...${'\n'}Coming Soon...${'\n'}Coming Soon...${'\n'}Coming Soon...${'\n'}Coming Soon...${'\n'}Coming Soon...${'\n'}Coming Soon...${'\n'}Coming Soon...${'\n'}Coming Soon...${'\n'}Coming Soon...${'\n'}Coming Soon...${'\n'}Coming Soon...${'\n'}Coming Soon...${'\n'}Coming Soon...`
    },
    {
        id: 1,
        heading2: 'Lab #2',
        content2: 'Basic Circuits',
        heading3: 'Objective:',
        content3: `The purpose of this project is to demonstrate the use of Boolean logic and build basic circuits. Note that this lab assignment is group assignment with each group having 2 students.${'\n\n'}The predominant storage inside a computer systems are on disks drives. SCSI disks are the standard disks in most Unix Workstations from Sun, HP, SGI, and other vendors. They are also the standard disks in Macintoshes and Higher-end Intel PC\'s, especially network servers. Consider the Wide Ultra4 SCSI which transfers data packets in 16-bit bursts at 160 MHz with a maximum throughput of 320 MB/sec. The data transfers at higher rates can result in random-noise pulse changes from a 0 to 1 and 1 to a 0. As the speed of processors and electronic communications increases, these parity flips become more prevalent and the inability to detect when these errors occur can be fatal. As a Design Engineer you have been requested to create system for the transmission of these 8-bit packets from an I/O Controller to Memory using Error Correcting Code over 12-bit data bus line. Wide SCSI contains a 68bit bus; however for the sake of simplification we are only concerned with the data bits. The other bits in the SCSI bus are for bus arbitration, synchronization, power management, etc. In this project, we will use even parity.${'\n\n'}`,
        image1: `cs-course-04-img-01`,
        content4: `Transmission Vectored Bit: A 4-Bit Parity Vector (P${'\u2081'}-P${'\u2084'}) are interlaced with the 8-bit${'\n\n'}Data Vector (D${'\u2081'}:D${'\u2088'}) and an additional parity bit P${'\u2085'} is appended to the 12-bit hamming code to ensure that the entire 13-bit vector is even parity: P${'\u2081'} P${'\u2082'} D${'\u2081'} P${'\u2083'} D${'\u2082'} D${'\u2083'} D${'\u2084'} P${'\u2084'} D${'\u2085'} D${'\u2086'} D${'\u2087'} D${'\u2088'} P${'\u2085'}${'\n\n'}1) Create an ECC Generator, at the I/O Controller from the 8-bit Data Vector. The output of the ECC Generator will be the 13-Bit Vector.${'\n\n'}2) Construct a 13-bit Data Transmission bus to send the 8-bit binary data and 5 parity bits over to Memory.${'\n\n'}3) Construct an ECC Detector at Main Memory that corrects for single bit errors.${'\n\n'}Generally an interrupt/error handler is used to handle errors from the OS, for this exercise we will use 3 Hex displays, 2 for data and 1 for an error status, for diagnostic purposes:${'\n\u2022'} In the event that no error has occurred, your design must display the data transferred using the 2 Hex data displays and a “0” as an error status.${'\n\u2022'} For single bit transmission errors, your system must correct the error and display the data along with “C” in the 3rd Hex Display.${'\n\u2022'} For 2-bit transmission errors, your design must display “E” in the error status display.`
    }
]

const dataFor_Course_04_Prog_03 = [
    {
        id: 2,
        heading2: 'Lab #3',
        content2: 'The Gottcha Anti-Theft Machine',
        heading5: `Solution:`,
        content5: `Coming Soon...${'\n'}Coming Soon...${'\n'}Coming Soon...${'\n'}Coming Soon...${'\n'}Coming Soon...${'\n'}Coming Soon...${'\n'}Coming Soon...${'\n'}Coming Soon...${'\n'}Coming Soon...${'\n'}Coming Soon...${'\n'}Coming Soon...${'\n'}Coming Soon...${'\n'}Coming Soon...${'\n'}Coming Soon...${'\n'}Coming Soon...`
    },
    {
        id: 1,
        heading2: 'Lab #3',
        content2: 'The Gottcha Anti-Theft Machine',
        heading3: 'Objective:',
        content3: `The purpose of this exercise is to create Finite State Machine (FSM) which can detect a certain eight digit binary sequence in a continuous serial binary input stream. When the correct sequence is detected, the single output signal should be a logical true value, in all other cases it should be a logical false. This exercise is designed to allow a gradual transition from strictly combinational circuits to a simple sequential circuit. Note that this lab assignment is group assignment with each group having exactly 2 students. There are many anti-theft devices on the market that attempt to foil a would-be robber from starting your car and driving off with it. One popular item has a keypad like a touch- tone telephone. In order to start your car, you must key in a secret four digit decimal code, such as "3719". It only "remembers" the most recent four digits you have keyed in. Thus the sequence "3723719" will let you start your car.${'\n'}For simplicity here we use a code based on a sequence of eight bits, and use two push buttons to enter a sequence serially. Each press of a button enters the corresponding digit.${'\n\n'}`,
        image1: `cs-course-04-img-02`,
        content4: `As you key in the bits in sequence, the device outputs E = 0 until the most recent eight bits agree with a built-in secret code byte. Then E switches to 1.${'\n\n'}The shut-off gottcha: If 16 bits are toggled in without the secret code being observed, the system shuts off and won\'t accept any more bits. Add a reset button to the system that presumably only the owner could control: when RESET is pressed the system is again enabled and can accept bits.${'\n\n'}Design and test (on LogicWorks) the miracle Gottcha machine.${'\n\n'}Do this in two different ways (thus designing and simulating two different circuits):${'\n\n'}1) The Factory Preset Model: For each group, the specific secret byte preset at the factory MUST be one of your team members’ secret keys. You can find your secret key at the end of this document. All groups, please explicitly explain which key is used for Factory Preset Model in your Readme file. Your group will receive zero point if you don’t follow this key use policy specified above. Do not use registers: solve for the finite state machine with the fewest states and flip-flops possible.${'\n\n'}2) The User-Programmable Model: This machine allows the car owner to enter a secret byte into a register. The user selects a byte using two hex keyboards, and presses an ENTERCODE pushbutton to store the code word. Now when the machine is used "in the field", the car owner enters a sequence using the same pushbutton arrangement as for the previous machine. When the sequence so entered matches the code word stored in the register, E goes HIGH. As with the Factory Preset Model, if more than 16 bits are entered without the proper sequence being observed, the system shuts off (until RESET is pressed).${'\n'}Some Hints:${'\n\n'}    1) These systems have no actual clock - the release of either pushbutton produces a${'\n'}    transition that is used to trigger the flip flops involved. The main flip flops in the circuit${'\n'}    are triggered by this transition.${'\n\n'}    2) You might put the outputs of the two pushbuttons into asynchronous inputs of a flip${'\n'}    flop, which therefore instantly stores the value (i.e. informs "which" pushbutton was${'\n'}    pressed) of the newest input bit. The output of this flip flop is then used as the actual${'\n'}    "input" value.${'\n\n'}    3) Use a shift register in part 2 to store the most recently received bits. Compare the shift${'\n'}    register output to the programmed code word. (So part 2 is simple)`
    }
]


