        id: 1,
        heading2: 'Program #2',
        content2: 'Key Types and Format',
        heading3: `Problem:`,
        content3: `1. Make empno the primary key for both empbb02, and infobb02, and make deptno the primary key for deptbb02. Use tne constraint naming convention give in class, both here and in all such problems below.${'\n\n'}2. Display ename and hiredate in the following way. The date should Google be displayed numerically as it would be done in France. Use the column alias French Date, and also Last Name. Use to learn about French dates.${'\n\n'}3. Make a suitable multi-column primary key for salsbb02.${'\n\n'}4. Make dname a unique key (unique constrant) in deptbb02, mostly to confirm that you CAN.${'\n\n'}5. When your display all of deptbb02, the motto "wraps", and appears can on a second line. Play with the COLUMN pseudo-op until you "fix" this. E.g. column dname format a10 limits the print width of dname to 10 bytes. It is temporary, of course.  You can Google this pseudo-op as needed.${'\n\n'}6. Make empno a foreign key in infobb02 (parent table is empbb02); also make deptno a foreign key in empbb02 (parent table is deptbb02)${'\n\n'}7. The boss dated a lady named Suzie Q, gave her a third of the franchise, and she ditched him for a race car driver. He will now not hire a player whose last name starts with "q". Make this a possible. To do that use  ALTER TABLE empbb02 MODIFY ename "check" constraint.${'\n\n'}8. Test your check constraint in 7. by trying to "hire" the player, Dan Quisenberry. NOTE: you will need to also widen the field width of ename, just to make hiring this guy VARCHAR2(16), or some other adequate width.${'\n\n'}9. In the terminology of foreign keys, you should not be allowed to drop a parent table, if it still has children. The parenting obligation is eternal... Verify this using your tables. BE CAREFUL not to lose a table you do not have backup for. :)${'\n\n'}10. Add Charles Bender to the roster.  Get his nickname, and position by Google.  Uniform number might be a challenge.${'\n\n'}11. Most of our team is in the Hall of Fame at Cooperstown. Add a column to infobb02 with the date of their hall of fame induction, and use Google to fill it in. Name the column hofdate. The command you need is ALTER TABLE infobb02 ADD...followed by column descriptor.`
