        content3: `${'\u2022'} Your project must consist of exactly the files listed above, with those exact names.${'\n\n\u2022'} You will use the files you have written from prog1 and prog2 in this project for processing the source code file.${'\n\n\u2022'} Public methods for the symtab class are not specified in this assignment. Your team will make all design decisions for this class. However, it is suggested that you use the map class for your data structure. This class will store KEY=VALUE pairs, where SYMBOL=ADDRESS or VALUE.${'\n\n\u2022'} You will revise this project later to add pass #2 and also add ehanced features. It is essential that you write your classes in a way that makes revision easy. Avoid large, monolithic blocks of code.${'\n\n\u2022'} Do not print comments in the listing file. As we will be adding another column for the machine code in the next assignment, format your output so that there will be room for an additional column.${'\n\n\u2022'} Remember that we have made a revision to the SIC/XE assemby language specifications. Integers which specify a dexadecimal value begin with the $ symbol. Integers without it are decimal.${'\n\n'}      Example:${'\n'}            alpha      LDA    $1000    .hex 1000, decimal 4096${'\n'}            beta        LDA    #1000    .hex 03E8, decimal 1000${'\n'}            gamma    LDA    #$1000   .hex 1000, decimal 4096${'\n'}            delta      WORD   1000     .hex 03E8, decimal 1000${'\n'}            zeta       WORD   $1000    .hex 1000, decimal 4096${'\n\n'}${'\u2022'} You do not need to handle forward references with pass one. If an EQU directive is a forward reference, leave it undefined in pass one.${'\n\n\u2022'} If a byte directive is a character constant, then the storage is the string length - 3. However, if it is hex, it takes two digits per byte (vs one digit per byte with character data). i.e.${'\n\n'}            FOO      BYTE   x${'\''}FFED${'\''}   .two bytes of storage (not four)${'\n'}            BAR      BYTE   c${'\''}EOF${'\''}    .three bytes of storage${'\n\n'}      You must insure that the BYTE directives are valid. If a hexadecimal value is${'\n'}      given, insure that it has an even number of characters. i.e. BYTE x${'\''}FFF${'\''} is${'\n'}      not valid.${'\n\n'}${'\u2022'} Addresses printed in the listing file must be in hexadecimal. Use uppercase letters for A..F. Do not put the $ in front of addresses printed in the listing file. It is unnecessary as only hexadecimal numbers are valid for addresses.${'\n\n\u2022'} For pass #1, you will not generate any machine code, only load the symbol table with all user- defined tokens and assign an address to every line in the source code file, including blank lines and lines that contain only comments or assembler directives.${'\n\n\u2022'} Your sic_xe class should have catch blocks to trap any exceptions that are thrown. Be sure to print a helpful error message—including the line number in the source file—if an error occurs.${'\n\n'}Your project will be graded based on:${'\n\n'}    ${'\u2022'} Code quality, and conformance to C++ standards.${'\n'}    ${'\u2022'} Correctly looks up opcodes and returns correct values.${'\n'}    ${'\u2022'} You must handle error conditions gracefully; under no circumstances should your program crash or terminate unexpectedly.`
