const dataFor_Course_08_Prog_01 = [
    {
        id: 2,
        heading2: 'Program #1',
        content2: `Chat Bots`,
        heading5: `Solution:`,
        content5: `Coming Soon...${'\n'}Coming Soon...${'\n'}Coming Soon...${'\n'}Coming Soon...${'\n'}Coming Soon...${'\n'}Coming Soon...${'\n'}Coming Soon...${'\n'}Coming Soon...${'\n'}Coming Soon...${'\n'}Coming Soon...${'\n'}Coming Soon...${'\n'}Coming Soon...${'\n'}Coming Soon...${'\n'}Coming Soon...${'\n'}Coming Soon...`
    },
    {
        id: 1,
        heading2: 'Program #1',
        content2: `Chat Bots`,
        heading3: `Details:`,
        content3: `You will do this by developing a project that manages multiple threads writing to a shared file (you aren’t building a chat server, the chat bots write to the shared/common file).You shall implement a program where several chat bots will run, each in their own thread, simultaneously but asynchronously with each other. Each bot shall write the specified text message to one, common shared resource, a file named QUOTE.txt. In order to prevent the data from getting corrupted by other bots, each bot needs to use an appropriate IPC mechanism/algorithm.${'\n\n'}1. When your program starts, it shall do the following:${'\n'}    a. Create a file, named QUOTE.txt, in the current directory (cwd).${'\n'}    b. Have your running process write it’s pid (Process ID) followed by a${'\n'}       Carriage Return and Newline in to the file.${'\n'}    c. Close the file QUOTE.txt.${'\n'}    d. Create a semaphore named FLAG which the threads will use to manage access${'\n'}        to the file QUOTE.txt.${'\n'}    e. Create 7 threads. Use the POSIX version of threads (i.e., pthread_create())${'\n'}    f. Block/wait for all seven threads to complete their work.${'\n'}    g. Once all threads are done, destroy the semaphore, then exit gracefully,${'\n'}       printing a friendly message to the console${'\n\n'}2. Each thread shall perform the following (note, each thread is running concurrently):${'\n'}    a. Periodically (even numbered threads - once every two seconds, odd${'\n'}       numbered threads – once every 3 seconds) get the semaphore SEM; once the${'\n'}       thread has SEM, it will proceed to do the following:${'\n'}            i. Open the file QUOTE.txt and write the thread’s tid (thread id)${'\n'}               followed by “The Quote” (followed by a Carriage Return and Newline)${'\n'}           ii. Write to the console (print to stdout) “Thread <thread id> is${'\n'}               running” followed by a newline.${'\n'}          iii. Close the file QUOTE.txt.${'\n'}           iv. Release the semaphore FLAG.${'\n'}    b. Repeat the above 7 times.${'\n'}    c. exit${'\n\n'}You will need to use the following POSIX system calls for creating and managing the semaphores with:${'\n'}sem_init(), sem_wait(), sem_post(), and sem_destroy().${'\n\n'}I will test your program by compiling it and executing it on rohan. Your program shall be written such that it compiles and executes cleanly when using the cc, gcc, CC, or g++ compiler You shall create a sub-directory named "a1" in your home directory. In it, you shall place your source file(s), your header file, your Makefile (see Blackboard for examples on Makefiles), and a README file (see Blackboard for README requirements). Your source files SHALL CONTAIN sufficient comments for making the source easy to read. Points will be taken off for poorly (or non) commented source. Name the executable "bots".${'\n\n'}    ${'\u2022'} Create ~/a1 by hand.${'\n'}    ${'\u2022'} Create source files, an include file, a Makefile, and a README file. Put them into ~/a1.${'\n'}    ${'\u2022'} The Makefile shall create an executable named "sem" in this same directory (~/a1).${'\n'}    ${'\u2022'} Here is a nice overview of threads [http://www.llnl.gov/computing/tutorials/pthreads/#Overview].${'\n'}    ${'\u2022'} The system call "system()" will NOT be allowed.You must work in pairs (team of 2) unless approved by the instructor.${'\n\n'}TURNING IN YOUR WORK:${'\n'}Make sure that all of your files (all source files, Makefile, README file, test files, etc) are in the a1 sub- directory of your class account and then follow the class assignment turnin procedures as specified in the instructions in Blackboard${'\n\n'}Note, only ONE of the team members turns in the assignment. State which class account is to be used for testing in the turnin and in the README file.`,
        heading4: `${'\n'}Quote:`,
        content4: `Even numbered threads: “Controlling complexity is the essence of computer programming."${'\n'}--Brian KerniganOdd numbered threads: “Computer science is no more about computers than astronomy is about telescopes.”${'\n'}--Edsger Dijkstra`
    }
]

