        content2: `Alarm Clock`,
        heading3: `Details:`,
        content3: `Design and implement an Alarm Clock${'\n\n'}Tom implement this alarm clock, you will need to display a running “wall clock“ (display hours : minutes : seconds), an “alarm clock” (displays a message at specified time of day), and a “timer” which displays a countdown timer (minutes : seconds) until 00 : 00 is reached. Note, this will require using multiple processes/threads, interrupts, and other IPC mechanisms.${'\n\n'}To run your program, the following is the command line format:${'\n'}  $> aclock h m s ct${'\n\n'}where h = hours, m = minutes, and s=second for the alarm and c = seconds for countdown timer (and is optional).${'\n\n'}You may use a single terminal window, each line printed to the terminal must identify if it’s time-of-day, timer, or alarm. Multiple terminals/displays could be used for extra credit. No process/thread may busy-wait. The time_${'\n\n'}Your program shall do the following:${'\n\n'}    ${'\u2022'} Upon startup, read the input to determine the alarm time and the timer run time.${'\n'}      If the user does not supply a value for the countdown timer, a default value of 10${'\n'}      seconds will be used. The program shall run until the user presses the x key${'\n'}      and then exits.${'\n'}    ${'\u2022'} The program shall create three child processes/threads, then busy-wait until the${'\n'}      user presses the x key. Note, if the x key is detected and the child processes/threads${'\n'}      are still running, the parent needs to wait until all child processes/threads are${'\n'}      complete and then exit.${'\n'}    ${'\u2022'} The fist child process/thread shall implement a clock which prints the hour, minute,${'\n'}      and second once every second (in human readable localtime).${'\n'}    ${'\u2022'} The second child process/thread shall implement an alarm clock that will display a${'\n'}      message at the specified time (message(s) to be determined by the student).${'\n'}    ${'\u2022'} The third child process/thread shall implement a countdown timer that prints the${'\n'}      minutes and seconds remaining, once every second, until 00:00 is reached.${'\n'}    ${'\u2022'} The parent process, upon receiving an “x” from the console (keyboard input from${'\n'}      the user), shall notify (signal, or pipe, or ..., etc, your choice of IPC mechanism) the${'\n'}      process’/threads telling them to terminate and then once they have completed (all${'\n'}      have exited), print a friendly message and then terminate itself.${'\n\n'}Your project shall include a README file, you will include it in your turn-in.${'\n\n'}Your program will be tested by compiling and executing on rohan. Your program shall be written such that it compiles and executes cleanly when using cc/gcc/CC/g++. Note - you must use a Makefile. You shall create a sub-directory named "a3" in your home directory. In it, you shall place all of your project files, including your Makefile. Your source files shall contain sufficient comments for making the source easy to read. Points will be taken off for poorly (or non) commented source. Name the executable "aclock". Also, create an archive file (tarball, zip) and upload to Blackboard (one student per project).${'\n\n'}    ${'\u2022'} Create ~/a3 by hand.${'\n'}    ${'\u2022'} Create all necessary project files. Put them into${'\n'}      ~/a3.${'\n'}    ${'\u2022'} The Makefile shall create an executable named "aclock" in this same directory (~/a3).${'\n'}    ${'\u2022'} The system call "system()" will NOT be allowed.${'\n'}    ${'\u2022'} You may work individually but are highly encouraged to work in teams of two (no${'\n'}      larger) on this assignment.${'\n'}    ${'\u2022'} You may use cc, CC, gcc, or g++ compiler on this assignment.`
