import React, { Component } from "react"
import { HashRouter, Route } from "react-router-dom"

import SideBar from "../sidebar"
import Title    from "../title"
import Basecard from "../basecard"
import Carousel from "../carousel"

const Introduction = () => ( <Basecard deck={ data_forIntro } /> )
const Introduction_SideBar = () => ( <SideBar items={ data_forIntro_SideBar } /> )

const Internship_01 = () => ( <SideBar items={ data_forIntern_01 } /> )
const Internship_01_Part_01 = () => ( <Basecard deck={ data_forIntern_01_Part_01 } /> )
const Internship_01_Part_02 = () => ( <Basecard deck={ data_forIntern_01_Part_02 } /> )
const Internship_01_Part_03 = () => ( <Basecard deck={ data_forIntern_01_Part_03 } /> )
const Internship_01_Part_04 = () => ( <Basecard deck={ data_forIntern_01_Part_04 } /> )

const Internship_02 = () => ( <SideBar items={ data_forIntern_02 } /> )
const Internship_02_Part_01 = () => ( <Basecard deck={ data_forIntern_02_Part_01 } /> )

class Industry extends Component {
    render() {
        return (
          <HashRouter basename="/industry">
            <div class="app-container">
              <Title items={ data_forTitle } />
              <Carousel items={ data_forCarousel } />

              <Route path="/" exact component={ Introduction_SideBar } />
              <Route path="/" exact component={ Introduction } />

              <Route path="/intern-01" component={ Internship_01 } />
              <Route path="/intern-01" exact component={ Internship_01_Part_01 } />
              <Route path="/intern-01/part-01" exact component={ Internship_01_Part_01 } />
              <Route path="/intern-01/part-02" component={ Internship_01_Part_02 } />
              <Route path="/intern-01/part-03" component={ Internship_01_Part_03 } />
              <Route path="/intern-01/part-04" component={ Internship_01_Part_04 } />
              
              <Route path="/intern-02" component={ Internship_02 } />
              <Route path="/intern-02" exact component={ Internship_02_Part_01 } />
              <Route path="/intern-02/part-01" exact component={ Internship_02_Part_01 } />
            </div>
          </HashRouter>
        );
    }
}

