        heading1: `STATUS:`,
        content1: `TYPE:${'\n'}DURATION:${'\n'}PROGRESS:${'\n'}CLEARANCE:`,
        content1add: `PART-TIME${'\n'}4 MONTHS${'\n'}COMPLETED${'\n'}SECRET`,
