        content2: `Planning and Decision Aid Support`,
        heading3: `BACKGROUND:`,
        content3: `Naval Information Warfare Center Pacific (NIWC Pacific), formerly known as "SSC Pacific (SPAWAR Systems Command Pacific)", is an organization within the United States Navy and provide military with essential capabilities in the areas of command and control, communications, computers, intelligence, surveillance, and reconnaissance (C4ISR), cyber, and space. Based in San Diego, California, NIWC Pacific is ranked as a top generator of patents and license agreements (155 patent disclosures, 100 patent applications filed, and 50 patents issued in fiscal year 2017).`,
        heading4: `${'\n'}EXPERIENCE:`,
        content4: `Funded by the Research Foundation at San Diego State University (SDSU), I was hired two times for a Student Contractor position during my Junior and Senior year as an undergraduate. The job allowed remote work and flexible hours in order to mandate studying as top priority. While working for the position at NIWC Pacific, I was able to gain real world experience and the opportunity to achieve a Secret Security Clearance. Some of the experience included working on mission critical systems, collaborating as part of a team, implementing with state-of-the-art technologies, and more explained later. The following next slides will now showcase in technical details the work and results my team and I produced for the task assigned.${'\n\n'}`
